.table-responsive {
    margin-bottom: 24px;

    overflow-x: auto;
    
    &::-webkit-scrollbar {
        height: 8px;
        width: 8px;
    }

    &::-webkit-scrollbar-track {
        background: $neutral-100;
        border-radius: 4px;
    }

    &::-webkit-scrollbar-thumb {
        background: $neutral-500;
        border-radius: 4px;

        &:hover {
            background: darken($neutral-500, 10%);
        }
    }
}

@mixin gray-table-th {
    background-color: $neutral-100;
    color: $gray;
    line-height: 1.3;
    vertical-align: middle;
}

.table.gray-table {
    line-height: 1.2;
    caption {
        caption-side: top;    /* 預設在表格上方 */
        background-color: $neutral-100;
        color: $gray;
        font-size: 1.125rem;
        padding: 10px 20px;
        border: 1px solid $neutral-300;
    }
    font-size: 1.125rem;
    td {
        border: 1px solid $neutral-300;
        padding: 20px 18px;
    }
    >thead {
        th {
            padding:10px 18px;
            border: 1px solid $neutral-300;
            border-bottom: 8px solid $neutral-300;
            white-space: nowrap;
            @include gray-table-th;
        }
    }
    > tbody {
        border: 1px solid $neutral-300;
        >tr>th:first-child {
            width: 183px;
            border-bottom: 1px solid $neutral-300;
            padding-right: 24px;
            @include gray-table-th;
        }
    }
    > tfoot {
        border-top: 8px solid $neutral-300;
        tr {
            td {
                background-color: $light-teal;
                color: $gray;
            }
        }
    }

    &.align-middle {
        td {
            vertical-align: middle;
        }
    }
    button.sort {
        color: $neutral-400;
        &.up {
            i:first-child {
                color: $neutral-700;
            }
        }
        &.down {
            i:last-child {
                color: $neutral-700;
            }
        }
    }
}

.table.side-table {
    > tbody {
        >tr>th:first-child {
            width: 183px;
            border-right: 8px solid $neutral-300;
            border-bottom: 1px solid $neutral-300;
            text-align: end;
            padding-right: 24px;
            @include gray-table-th;
        }
        @media (max-width: $lg) {
            border: none;
            > tr {
                display: flex;
                flex-direction: column;
                gap: 8px;
                border-top: 1px dashed $neutral-300;
                border-bottom: 1px dashed $neutral-300;
                padding: 20px 8px;
                > th:first-child {
                    display: block;
                    font-size: 0.78125rem;
                    font-style: normal;
                    font-weight: 500;
                    line-height: 16px; /* 128% */
                    letter-spacing: 0.125px;
                    color: $gray;
                    margin-bottom: 8px;
                    text-align: start;
                    background: unset;
                    border: 0;
                    padding: 0;
                    margin: 0;
                    background-color: transparent !important;
                    border-bottom: 0 !important;
                }
                > td {
                    border: none;
                    padding: 0;
                    margin: 0;
                }
            }
        }
    }
}

.cyan-table {
    thead {
        tr {
            border-bottom: 2px solid $cyan;
            th {
                color: $cyan;
                padding-block: 16px;
                text-align: center;
                white-space: nowrap;
                vertical-align: middle;
                line-height: 1.3;
            }
        }
    }
    td {
        vertical-align: middle;
        line-height: 1.3;
        text-align: center;
    }
    

    tbody {
        tr:nth-child(even) td {
            background-color: $neutral-100;
        }
    }
    
    @media (max-width: $md) {
        thead {
            display: none;
        }
        tbody {
            tr {
                display: grid;
                grid-template-columns: 1fr;
                padding: 16px;

                td {
                    text-align: start;
                    width: 100%;
                    display: flex;
                    align-items: center;
                    flex-wrap: wrap;
                    gap: 6px;
                    &:before {
                        content: attr(data-th);
                        font-weight: 500;
                        color: $gray;
                        white-space: nowrap;
                    }
                }
            }
            tr:nth-child(even) td {
                background-color: transparent;
            }
            tr:nth-child(even) {
                background-color: $neutral-100;
            }
        }
    }
}
.teal-table {
    thead {
        tr {
            border-bottom: 2px solid $teal;
            th {
                color: $teal;
                padding-block: 16px;
                text-align: center;
                white-space: nowrap;
                vertical-align: middle;
                line-height: 1.3;
            }
        }
        tr.total-row {
            border-top: 8px solid $teal;
            border-bottom: 1px solid $teal;
            td {
                background-color: $light-teal;
                color: $neutral-700;
                padding-block: 10px;
            }
        }
    }
    td {
        vertical-align: middle;
        line-height: 1.3;
        text-align: center;
    }
    

    tbody {
        tr:nth-child(even) td {
            background-color: $neutral-100;
        }
        tr td:first-child:has(.checkPlan:checked) {
            background-color: $light-teal-100;
            ~td {
                background-color: $light-teal-100;
            }
        }
        .btn-sm {
            min-width: 33px;

        }
    }

    td,th {
        padding: .4rem .2rem;
    }
    
    @media (max-width: $md) {
        thead {
            display: none;
        }
        tbody {
            tr {
                display: grid;
                grid-template-columns: 1fr;
                padding: 16px;

                td {
                    text-align: start;
                    width: 100%;
                    display: flex;
                    align-items: center;
                    flex-wrap: wrap;
                    gap: 6px;
                    &:before {
                        content: attr(data-th);
                        font-weight: 500;
                        color: $gray;
                        white-space: nowrap;
                    }
                }
            }
            tr:nth-child(even) td {
                background-color: transparent;
            }
            tr:nth-child(even) {
                background-color: $neutral-100;
            }

        }
    }


}

.table-responsive:has(.teal-table) {
    overflow-x: unset;
    @media (max-width: 1024px) {
        overflow-x: auto;
    }
}

.table {
    button.sort {
        border: none;
        background-color: transparent;
        padding: 0;
        margin: 0;
        color: $pale-green;
        width: 24px;
        height: 24px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        i {
            font-size: 1rem;
            line-height: 0.2;
        }
        &.up {
            i:first-child {
                color: $teal;
            }
        }
        &.down { 
            i:last-child {
                color: $teal;
            }
        }

    }
}

.sub-table {
    overflow-x: auto;
    width: 100%;

    table {
        min-width: 1000px;
        line-height: 1.3;
        td {
            vertical-align: middle;
            color: $gray;
        }

        tbody:nth-child(odd) td {
            background-color: #F5FAFF;
        }
        tbody {
            tr:first-child {
                td {
                    border-top: 8px solid $neutral-300;
                }
            }

        }
        tbody.table-item {
            tr:first-child {
                td:first-child  {
                    font-size: 1.5rem;
                    font-weight: 700;
                    text-align: center;
                }
            }
            tr~tr {
                td:first-child {
                    text-align: end;
                }
            }
        }
        tfoot {
            border-top: 8px solid $neutral-300;
            tr {
                td {
                    background-color: $light-teal;
                    text-align: end;
                }

            }
        }
    }
}

// 拖曳排序相關樣式
.sortable-ghost {
    opacity: 0.5;
    background-color: #f0f8f7 !important;
    border: 2px dashed #26A69A !important;
}

.sortable-chosen {
    background-color: #e8f5e8 !important;
    box-shadow: 0 4px 12px rgba(38, 166, 154, 0.15);
    transform: scale(1.005);
    transition: all 0.2s ease;
}

