// 拖曳按鈕樣式
.btnDrag {
    cursor: grab;
    transition: all 0.2s ease;
    
    &:active {
        cursor: grabbing;
        transform: scale(0.95);
    }
    
    &:hover {
        background-color: $teal-dark-hover !important;
        color: #fff;
    }
}

//隱藏表格某一欄
@mixin hide-table-column($column, $type: 'nth-child') {
    @if $column == 'first' {
        td:first-child,
        th:first-child {
            display: none;
        }

    } @else if $column == 'last' {
        td:last-child,
        th:last-child {
            display: none;
        }
    } @else {
        td:#{$type}(#{$column}),
        th:#{$type}(#{$column}) {
            display: none;
        }
    }
}
.hide-col {
    &-first {
        @include hide-table-column('first');
    }
    &-2 {
        @include hide-table-column(2);
    }
    &-3 {
        @include hide-table-column(3);
    }
    &-4 {
        @include hide-table-column(4);
    }
    &-5 {
        @include hide-table-column(5);
    }
    &-6 {
        @include hide-table-column(6);
    }
    &-7 {
        @include hide-table-column(7);
    }
    &-8 {
        @include hide-table-column(8);
    }
    &-9 {
        @include hide-table-column(9);
    }
    &-10 {
        @include hide-table-column(10);
    }
    &-last {
        @include hide-table-column('last');
    }
}

//某一欄文字靠右
// Mixin for table column text alignment
@mixin table-text-align($column, $align: 'right') {
    @if $column == 'first' {
        td:first-child {
            text-align: #{$align};
        }
    } @else if $column == 'last' {
        td:last-child {
            text-align: #{$align};
        }
    } @else {
        td:nth-child(#{$column}) {
            text-align: #{$align};
        }
    }
}

.table-text-right-col {
    &-first {
        @include table-text-align('first');
    }
    &-last {
        @include table-text-align('last');
    }
}

